<template>
    <div class="list">
        <cell class="item" v-for="(item, index) in accountList" :key=" index " 
            title="账号" label="账户类型" :value="item.ACCOUNT" :desc="item.ACCTLEVEL | accountTypeFilter"
            @click.native='next(item)' is-link>
        </cell>
    </div>
</template>

<script>


import * as service from '@/services/public';
// import { mapGetters, mapActions, mapState } from 'vuex';

export default {
    data() {
        return {
            accountList: [],
            accountInfo: {
                OPERTYPE: '', // 操作类型
                PAYER_ACCOUNT: '', // 操作账号
                ACCTLEVEL: '', // 账户类别
                PAYEE_ACCOUNT: '', // 绑定账号
                ACCTSOURCE: '', // 账户来源
                BINDACCTTYPE: '', // 绑定账户类型
                PAY_PWD: '', // 支付密码
                accountTypeName: '',
                operationType: '',
                userName: '',
                userIdNo: '',
                phone: ''
            }
        };
    },
    created() {
        this.accountList = Object.assign([], this.$store.state.accountLiftLevel.search.accountList);
        if(this.accountList == null || this.accountList.length == 0){
            service.getCommonAccountList({'flagType': '3'}).then((res) => {
                this.accountList = res.LIST;
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
        next (item){
            this.accountInfo.PAYER_ACCOUNT = item.ACCOUNT;
            this.accountInfo.ACCTLEVEL = item.ACCTLEVEL;
            this.accountInfo.PAYEE_ACCOUNT = item.BINDACCT;
            this.accountInfo.ACCTSOURCE = '0';
            this.accountInfo.BINDACCTTYPE = item.BINDACCTTYPE;
            this.$router.push( 'info1' );
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateAccountLiftLevelSearch', {accountList: this.accountList} );
        }else{
            this.$store.dispatch('clearAccountLiftLevelSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.list
    {
        .item
        {
            .cell-label,.cell-desc
            {
                font-size: 13px!important;
                color: #333!important;
            }
        }
    }
</style>



