<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="客户姓名：" :value="accountInfo.USER_NAME"></mt-cell>
            <mt-cell label="账户类型：" :value="accountInfo.ACCT_TYPE | accountTypeFilter"></mt-cell>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">设置交易密码:</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-field label="交易密码：" placeholder="请输入交易密码" v-model="payPass.pwd1"></mt-field>
            <mt-field label="再次输入：" placeholder="请输入输入确认密码" v-model="payPass.pwd2"></mt-field>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
            <mt-button @click="next()" size="large" type="primary">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/my/account-opening';

export default {
    data() {
        return {
            accountInfo: {},
            payPass: {
                pwd1: '',
                pwd2: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign({}, this.$store.state.accountOpening.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.payPass);
            if (this.$validate.isEmpty(_data.pwd1)) {
                this.doMsg('请输入交易密码');
                return false;
            }
            if (this.$validate.isEmpty(_data.pwd2)) {
                this.doMsg('请输入输入确认密码');
                return false;
            }
            if (_data.pwd1.length != 6) {
                this.doMsg('交易密码长度为6位数字');
                return false;
            }
            if (_data.pwd1 != _data.pwd2) {
                this.doMsg('两次密码不一致');
                return false;
            }

            this.accountInfo.PAY_PWD = 'sign:214F9E71|rand:DB2D050F|epin:468DF7594A59C7A7|csn:340101199105050073|version:03|counter:24D2FAF0';
            service.getAccountOpeningSuccess({
                'ACCT_TYPE': this.accountInfo.ACCT_TYPE,
                'PAYER_ACCOUNT': this.accountInfo.PAYER_ACCOUNT,
                'ACCTSOURCE': this.accountInfo.ACCTSOURCE,
                'BINDACCTTYPE': this.accountInfo.BINDACCTTYPE,
                'PAY_PWD': this.accountInfo.PAY_PWD
            }).then((res) => {
                this.$ls.setItem("accountOpeningSuccess", res.data);
                this.$router.push('/account-opening/success');
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        doMsg(msg) {
            this.$toast(msg);
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



