<template>
	<div class="my-bank-card">
		<mt-navbar fixed v-model="selected">
  			<mt-tab-item id="1">储蓄卡({{AccountList.length}})</mt-tab-item>
  			<mt-tab-item id="2">信用卡({{creditList.length}})</mt-tab-item>
		</mt-navbar>
		<div class="my-bank-card-contain">
			<mt-tab-container v-model="selected">
	  			<mt-tab-container-item id="1">
	    			<bank-card :bank-card-list="accountList" @bank-card-click="bankCardClick" class="contain-card-list"></bank-card>
	    			<div v-if="AccountList.length > accountList.length" class="show-more">
		    			<div class="show-more-line">
		    				<a @click="showMore()" href="javascript:;">
		    					<span>展示更多已添加的卡</span>
		    				</a>
		    			</div>
	    			</div>
	    			<div class="tip-not-add" :class="{'tip-none-add': account.length <= 0}">
	    				{{hasAccountTip}}
					</div>
					<div v-for="item in account" class="bank-card-account">
						<div class="card-account-left">
							<p class="number">{{item.CARD | formatAccount}}</p>
							<p class="bank">{{item.FJGM}}</p>
							<p class="money">
								<span>余额：</span>
								<span>{{item.VAILABLE_BALANCE}}</span>
								<span>元</span>
							</p>
						</div>
						<div class="card-account-right">
							<a @click="addAccount(item)" href="javascript:;">
								<i class="add-account-icon"></i><br>
								<span class="add-account-span">添加</span>
							</a>
						</div>
					</div>
					<div class="fixed-footer">
						<mt-button @click.native="addBankCard" size="large" type="primary">
							<img src="../../../assets/img/bank/card/add-white@2x.png" class="icon" slot="icon">手动添加银行卡
							</mt-button>
					</div>
	  			</mt-tab-container-item>
	  			<mt-tab-container-item id="2">
	    			<bank-card :bank-card-list="creditList" @bank-card-click="creditClick" class="contain-card-list" cardType="credit"></bank-card>
	    			<div class="fixed-footer">
						<mt-button @click.native="addCredit" size="large" type="primary">
							<img src="../../../assets/img/bank/card/add-white@2x.png" class="icon" slot="icon">手动添加信用卡
						</mt-button>
					</div>
	  			</mt-tab-container-item>
			</mt-tab-container>
			
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			accountList: [],
			selected: '1',
			account: [],
	        creditList: [],
	        AccountList: [],
	        accountListNum: 10
		};
	},
	computed: {
		hasAccountTip() {
			return this.account.length > 0 ? '未添加到手机银行的账户' : '您的账号已全部添加至手机银行！';
		}
	},
	created(){
		this.getBankCardList();
		this.getBankCreditList();
	},
	methods: {
		addAccount(detail){
			this.$ls.setItem('bankCards', detail);
			this.$router.push({name: 'MyBankCardAddAccount'});
		},
		addBankCard(){
			if(this.account.length == 0){
	            this.$alert('您的账号已全部添加至手机银行！', {
                    btn: { text: '确定' }
                });
			}else{
				this.$router.push({name: 'MyBankCardAddCard'});
			}
		},
		addCredit() {
			this.$router.push({name: 'MyBankCardCreditAdd'});
		},
		bankCardClick(item){
			this.$ls.setItem('bankCards', item);
			this.$router.push({name: 'MyBankCardSet'});
		},
		creditClick(item){
			this.$ls.setItem('creditMsg', item);
			this.$router.push({name: 'CreditManageCardSet'});
		},
		getBankCardList() {
			service.getBankCardList({
				ACCT_TYPE: 1,
				OPT_TYPE: 1
			}).then((data) => {
				if(data.LIST){
					this.AccountList = data.LIST;
					this.accountList = this.AccountList.slice(0, this.accountListNum);
				}
				if(data.UNUSELIST){
					this.account = data.UNUSELIST;
				}
			}, (err) => {
				console.log(err);
			});
		},
		getBankCreditList() {
			service.getBankCreditList().then(data => {
				this.creditList = data.LIST;
			}, err => {
				console.log(err);
			});
		},
		showMore(){
			this.accountListNum += 10;
			this.accountList = this.AccountList.slice(0, this.accountListNum);
		}
	},
	beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] !== from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$ls.removeItem('bankCards');
            this.$ls.removeItem('bankCards');
        }
        next( true );
    }
};

</script>

<style lang="scss">

.my-bank-card{
	.mint-navbar{
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.03);
		.mint-tab-item{
			padding: 40px 0;
		}
		.mint-tab-item.is-selected{
			position: relative;
			border-bottom: 0 solid #FF511A;
	    	color: #FF511A;
		}
		.mint-tab-item.is-selected:after{
			content: '';
			position: absolute;
			left: 50%;
			bottom: 22%;
			transform: transLateX(-50%);
			width: 60px;
			height: 4px;
			background-color:  #FF511A;
		}
		.mint-tab-item-label{
			font-size: 16px;
			letter-spacing: 0.25px;
		}
	}
	.my-bank-card-contain{
		padding-top: 140px;
		padding-bottom: 190px;
		.contain-card-list{
			padding-left: 30px;
			padding-right: 30px;
		}
		.show-more{
			height: 42px;
			margin: 60px 30px 30px;
			.show-more-line{
			    height: 1px;
			    border-top: 1px dashed #d6d6d6;
			    text-align: center;
			}
			.show-more-line a{
			    position: relative;
			    top: -16px;
			    background: #f3f3f3;
			    font-size: 15px;
			    color: #666666;
			    padding: 0 60px 0 25px;
			}
			.show-more-line a:after{
				content: '';
				position: absolute;
				top: 49.5%;
				transform: transLateY(-50%);
				right: 15px;
				display: inline-block;
				width: 32px;
				height: 32px;
				@include image-set('../../../assets/img/bank/card/show');
				background-size: 100% 100%;
			}
		}
	}
	.tip-not-add{
		margin: 60px 30px 16px;
		font-size: 13px;
		color: #272727;
	}
	.tip-none-add{
		text-align: center;
		color: #B5B5B5;
	}
	.bank-card-account{
		padding: 30px 0 30px 30px;
		height: 210px;
		background-color: #fff;
		.card-account-left{
			float: left;
			.number{
				font-size: 19px;
				color: #272727;
			}
			.bank{
				margin: 16px 0px;
				font-size: 13px;
				color: #B5B5B5;
				max-width: 520px;
				@include ellipsis();
			}
			.money>{
				span:nth-child(1){
					font-size: 13px;
					color: #272727;
				}
				span:nth-child(2){
					font-size: 15px;
					color: #FF511A;
				}
				span:nth-child(3){
					font-size: 15px;
					color: #272727;
				}
			}
		}
		.card-account-right{
			float:right;
			width: 155.14px;
			margin-top: 28px;
			border-left: 1px solid #E4E5E6;
			text-align: center;
			.add-account-icon{
				display: inline-block;
				width: 48px;
				height: 48px;
				background-size: auto 100%;
				background-repeat: no-repeat;
	            @include image-set('../../../assets/img/bank/card/add');
			}
			.add-account-span{
				margin-top: 16px;
				font-size: 13px;
				color: #308CF8;
			}
		}
	}
	.bank-card-account:after{
		content: "clear"; 
		display: block; 
		height: 0; 
		clear: both; 
		overflow: hidden; 
		visibility: hidden;
	}
	.bank-card-account + .bank-card-account{
		margin-top: 30px;
	}
	.fixed-footer{
		.icon{
			width: 36px;
			height: 36px;
		}
	}
}

</style>


