<template>
	<div class="my-bank-card-credit-add">
		<div class="head-hint hint-reset">请输入信用卡卡号</div>
		<div class="field-part">
			<mt-field v-model="cardNum" label="信用卡卡号" placeholder="仅限安徽农金系统内的信用卡"></mt-field>
		</div>
		<div class="tip-hint">卡片已添加</div>
		<div class="button-bottom">
			<mt-button @click="next" type="primary" size="large" :disabled="!cardNum">下一步</mt-button>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			cardNum: ''
		};
	},
	methods: {
		next() {
			this.$router.push({name: 'MyBankCardCreditDetail', params: {cardNum: this.cardNum}});
		},
		noneActive() {
			this.$confirm('该信用卡尚未激活，如需添加卡片，请先激活', {
                yes: { text: '马上激活' },
                no: { text: '以后再说' }
            }).then((result)=>{
                this.$router.push({name: 'OneActiveDetail', params: {card: this.cardNum, fromAdd: true}});
            }, (result)=>{
                console.log(result);
            });
		},
		noneTransactionPwd() {
			this.$hybrid.call({
                method: 'confirm',
                params: {
                    title: '该信用卡尚未申请交易密码，如需添加卡片，请先申请密码',
                    message: '',
                    confirmButtonText: '马上申请'
                },
                callback: (params) => {
                	if(params.action == 1){
						this.$router.push({name: 'CreditManagePwdTransactionApplySet', params: {card: this.cardNum, fromAdd: true}});
					}
                }

            });
		}
	}
};

</script>

<style lang="scss">
	
.my-bank-card-credit-add{
	.hint-reset{
		margin-top: 0;
		padding-top: 20px;
	}
}

</style>


