<template>
    <div class="list">
        <cell class="item" v-for="(item, index) in accountList" :key=" index " 
            title="账号" label="账户类型" :value="item.ACCOUNT" :desc="item.ACCT_TYPE | accountTypeFilter"
            :to="{ path: 'info', query: { account: item.ACCOUNT, type:item.ACCT_TYPE } }"  is-link>
        </cell>
    </div>
</template>

<script>


// import * as service from '@/services/my/relationship-change';
import * as service from '@/services/public';

export default {
    data() {
        return {
            accountList: []
        };
    },
    created() {
        this.accountList = Object.assign({}, this.$store.state.relationshipChange.search).accountList;
        if(this.accountList == null || this.accountList.length == 0){
            service.getCommonAccountList({'flagType': '3'}).then((res) => {
                this.accountList = res.LIST;
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateRelationshipChangeSearch', {accountList: this.accountList} );
        }else{
            this.$store.dispatch('clearRelationshipChangeSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.list
    {
        .item
        {
            .cell-label,.cell-desc
            {
                font-size: 13px!important;
                color: #333!important;
            }
        }
    }
</style>



