<template>
    <div>
        <div class="field-part long-label-8">
             <mt-field label="单笔限额(元)" placeholder="点击可设置"  v-model=" dataForm.singleLimit "></mt-field>
             <span class="validate" v-if=" validate.validate_1 != '' ">{{validate.validate_1}}</span>
        </div>
        
        <div class="field-part long-label-8">
             <mt-field label="单日限额(笔)" placeholder="点击可设置"  v-model=" dataForm.dayLimit "></mt-field>
             <span class="validate" v-if=" validate.validate_2 != '' ">{{validate.validate_2}}</span>
        </div>
        <div class="field-part long-label-8">
            <mt-field label="单日交易笔数(笔)" placeholder="点击可设置"  v-model=" dataForm.dayLimitTransaction "></mt-field>
            <span class="validate" v-if=" validate.validate_3 != '' ">{{validate.validate_3}}</span>
        </div>
        <div class="field-part long-label-8">
            <mt-field label="年累计交易限额(元)" placeholder="点击可设置"  v-model=" dataForm.yearLimitTransaction "></mt-field>
            <span class="validate" v-if=" validate.validate_4 != '' ">{{validate.validate_4}}</span>
        </div>
        <div class="field-reminder">
            <h4>
                温馨提示：
            </h4>
            <p>
                在手机银行上只可以调低单笔限额、单日限额。如需调高单笔限额、单日限额，需要通过专业版网银或前往柜台办理。
            </p>
        </div>
        <div class="field-action">
            <submit-button @submit="save()" :submitStatus="submitStatus" value="保存" size="large" type="submit"></submit-button>
        </div>
    </div>
    
</template>

<script>

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            submitStatus: false,
            dataForm: {
                singleLimit: '',
                dayLimit: '',
                dayLimitTransaction: '',
                yearLimitTransaction: ''
            },
            validate: {
                validate_1: '',
                validate_2: '',
                validate_3: '',
                validate_4: ''
            }
        };
    },
    created(){
    },
    mounted(){
    },
    methods: {
         save (){
            console.log(123);
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.singleLimit && item.dayLimit && item.dayLimitTransaction && item.yearLimitTransaction){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
                if(item.singleLimit > 5000 ){
                    this.validate.validate_1 = "超过最大额度(50,000)";
                }else{
                    this.validate.validate_1 = "";
                }
                if(item.dayLimit > 5000 ){
                    this.validate.validate_2 = "超过最大额度(50,000)";
                }else{
                    this.validate.validate_2 = "";
                }
            },
            deep: true
        }
    }
};
</script>

<style lang="scss">
.validate {
    display: inline-block;
    margin: 20px 0px 0px 50px;
    font-family: PingFangSC-Regular;
    font-size: 13px;
    color: #FF511A;
    line-height: 18px;
}
</style>



