<template>
	<div class="my-bank-card-credit-detail">
		<div class="field-part">
			<mt-cell title="信用卡卡号" :value="detail.PAYER_ACCOUNT"></mt-cell>
		</div>
		<div class="field-part">
			<div class="head-hint">请输入卡片信息</div>
			<mt-field v-model="detail.CVN2" label="CVN2" placeholder="卡片背面签名栏后3位数字" :attr="{ maxlength: 3 }"></mt-field>
			<mt-field v-model="detail.CARDVAL" label="卡片有效期" placeholder="卡片正面“/”的4位数字，如0421" :attr="{ maxlength: 4 }"></mt-field>
			<mt-field v-model="detail.ACCT_ALIAS" label="别名" placeholder="(选填)请输入别名，例如：理财卡" :attr="{ maxlength: 12 }"></mt-field>
			<cell-password :options=" cellPassword "></cell-password>
		</div>
		<div class="tip-hint">卡片信息有误，请重新输入</div>
		<div class="button-bottom">
			<mt-button @click="showDialogSmsVerify" type="primary" size="large">下一步</mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			detail: {
				PAYER_ACCOUNT: '',
				ACCT_ALIAS: '',
				CVN2: '',
				CARDVAL: ''
			},
			cellPassword: {
                title: '交易密码',
                rule: 'number'
            },
            dialogSmsVerify: {
                title: '输入验证码完成添加',
                btnConfirmText: '完成添加',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
		showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.addCredit();
            }, (data) => {
            });
        },
        addCredit() {
        	service.addCredit({
        		ACCT_TYPE: '3',
        		PAYER_ACCOUNT: this.detail.PAYER_ACCOUNT,
        		ACCT_NAME: '',
        		ACCT_ALIAS: this.detail.ACCT_ALIAS,
        		CVN2: this.detail.CVN2,
        		CARDVAL: this.detail.CARDVAL,
        		OPT_TYPE: '3',
        		PAY_PWD: '',
        		MSGCODE: '',
        		INDEXCODE: ''
        	})
        	.then((data) => {
        		this.$router.push({name: 'MyBankCardCreditSuccess'});
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.detail.PAYER_ACCOUNT = this.$route.params.cardNum;
	}
};

</script>


