<template>
	<div class="my-bank-card-set">
		<div class="account-msg cell-part">
			<div class="name">{{cards.ACCT_NAME}}</div>
			<div class="bank">
				<span class="name">{{cards.FJGM}}</span>
				<span class="bank-type">{{cards.ACCT_TYPE}}</span>
			</div>
			<div class="number">{{cards.CARD}}</div>
			<ul class="type">
				<li>人民币卡</li>
				<li>账户类型：{{cards.ACCT_TYPE}}</li>
				<li>账户状态：{{cards.ACCT_STATUS}}</li>
			</ul>
		</div>
		<div class="cell-part">
			<mt-cell title="设置主账户">
  				<span v-if="cards.ACCT_DEF == 1" class="is-default-set">已设置</span>
  				<a @click="setDefault()" class="default-set" href="javascript:;" v-else>设置</a>
			</mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="setName()" title="设置别名" is-link>
  				<span>{{!cards.ALIAS ? '未设置' : cards.ALIAS}}</span>
			</mt-cell>
		</div>
		<div class="tip-hint">
			该账户状态异常，请到我行网点查询，或致电9669。
		</div>
		<a @click="deleteCard()" class="account-delete" href="javascript:;">从手机银行删除此卡</a>
	</div>
</template>

<script>
import service from '@/services/my/my-bank-card';

export default{
	data() {
		return {
			cards: {}
		};
	},
	methods: {
		setDefault() {
            // this.$alert('该卡片状态异常，无法设置为主账户', {
            //     btn: { text: '确定' }
            // });
            this.$confirm(`您确定要将此银行卡设为主账户？<br>${this.cards.FJGM}<br>${this.cards.CARD}`, {
                yes: { text: '确定' },
                no: { text: '取消' }
            }).then((result)=>{
                service.defaultBankCard({
					ACCOUNT: this.cards.CARD
				}).then(data => {
					this.cards.ACCT_DEF = 1;
					this.$ls.setItem('bankCards', this.cards);
				});
            }, (result)=>{
                console.log(result);
            });
		},
		setName() {
			this.$router.push({name: 'MyBankCardSetName'});
		},
		deleteCard() {
			if(this.cards.ACCT_DEF == 1){
				this.$alert('已设置为主账户，无法删除', {
	                btn: { text: '确定' }
	            });
			}else{
	            this.$confirm(`您确定要将此卡从手机银行删除？<br>${this.cards.FJGM}<br>${this.cards.CARD}`, {
	                yes: { text: '确定' },
	                no: { text: '取消' }
	            }).then((result)=>{
	                service.deleteBankCard({
	                	ID: '1',
						ACCT_TYPE: 1,
						ACCOUNT: this.cards.CARD,
						OPT_TYPE: 2
					}).then(data => {
						this.$ls.removeItem('bankCards');
						this.$router.push({name: 'MyBankCardSuccess', params: { type: 'delete' }});
					});
	            }, (result)=>{
	                console.log(result);
	            });
			}
		}
	},
	created(){
		this.cards = this.$ls.getItem('bankCards');
		console.log(this.cards);
	}
};

</script>

<style lang="scss">

.my-bank-card-set{
	.account-msg{
		position: relative;
		background-color: #fff;
		padding: 30px 50px 0;
		height: 284px;
		@include image-set('../../../../assets/img/bank/card/bg');
		.name{
			font-size: 15px;
			color: #666666;
			margin-bottom: 20px;
		}
		.bank{
			display: table;
			font-size: 15px;
			color: #666666;
			margin-bottom: 10px;
			.name{
				display: table-cell;
				padding-right: 20px;
				max-width: 560px;
				@include ellipsis();
			}
			.bank-type{
				position: relative;
				display: inline-block;
                width: 100px;
                height: 32px;
                font-size: 12px;
                color: #B5B6BE;
                text-align: center;
                line-height: 32px;
                @include image-set('../../../../assets/img/bank/card/label');
			}
		}
		.number{
			font-size: 15px;
			color: #272727;
			font-weight: normal;
            letter-spacing: 2.3px;
            font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
		}
		.type{
			position: absolute;
			display: table;
			width: 100%;
			left: 0;
			bottom: 20px;
			padding: 0 50px;
			font-size: 13px;
			color: #fff;
			li{
				display: table-cell;
			}
			li:nth-child(2){
				text-align: center;
			}
			li:nth-child(3){
				text-align: right;
			}
		}
	}
	.default-set{
		display: inline-block;
		color: #FF511A;
		font-size: 13px;
		padding: 12px 34px;
		border: 1px solid #FF511A;
		border-radius: 8px;
	}
	.is-default-set{
		font-size: 16px;
		color: #46C300;
	}
	.account-delete{
		display: block;
		padding: 30px 50px;
		margin-top: 80px;
		background-color: #fff;
		font-size: 15px;
		color: #FF511A;
		text-align: center;
	}
}

</style>


