<template>
    <div>
        <div class="field-part cross-card-info">
            <div class="img"></div>
            <ul>
                <li>
                    <span>银行卡号</span>
                    <span>{{ detail.account }}</span>
                </li>
                <li>
                    <span>所属银行</span>
                    <span>{{ detail.bankName }}</span>
                </li>
                <li>
                    <span>账户类型</span>
                    <span>{{ detail.type | accountTypeFilter }}</span>
                </li>
                <li v-if="detail.isopen=='true'">
                    <span>跨境交易服务</span>
                    <span class="isopen">已开通</span>
                </li>
                <li v-else>
                    <span>跨境交易服务</span>
                    <span>未开通</span>
                </li>
            </ul>
        </div>
        <div class="field-part" v-if="detail.isopen=='true'">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <span class="field-remark-dest">通过跨境交易管理可开通或关闭实体卡在境外的pos消费功能</span>
        <div class="field-part field-action" v-if="detail.isopen=='true'">
            <mt-button @click="pay('close')" size="large" type="primary">关闭跨境交易</mt-button>
        </div>
        <div class="field-part field-action" v-else>
            <mt-button @click="pay()" size="large" type="primary">开通跨境交易</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/my/cross-border-transaction';

export default {
    data() {
        return {
            detail: {},
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            }
        };
    },
    created(){
        this.detail = this.$ls.getItem("crossBorderTransactionDetail");
    },
    mounted(){
    },
    methods: {
        pay ( type ){
            if( type == 'close'){
                this.$confirm('确定关闭跨境交易功能吗？', {
                    yes: { text: '确定' },
                    no: { text: '取消' }
                }).then((result)=>{
                    this.crosee( 'close' );
                }, (result)=>{
                    console.log(result);
                });
            }else{
                this.crosee( 'open' );
            }
        },
        crosee (t){
            service.getCrossBorderTransactionSuccess().then((res) => {
                var params = {
                    path: 'success',
                    query: {
                        type: t
                    }
                };
                this.$router.push(params);
            }, (err) => {
                console.log( err );
            });
        }
    }
};
</script>

<style lang="scss">
.cross-card-info {
    width: 690px;
    height: 304px;
    margin: 40px auto 0 auto;
    overflow: hidden;
    background: #FFFFFF;
    border: 1px solid #EDEDED;
    box-shadow: 0 4px 15px 0 rgba(0,0,0,0.06);
    border-radius: 8px;
    .img {
        position: absolute;
        width: 365px;
        height: 304px;
        background-image: url('../../../../assets/img/credit/card@2x.png');
        background-image: image-set(url('../../../../assets/img/credit/card@3x.png') 3x);
        background-repeat: no-repeat;
        background-size: 100%;
    }
    ul {
        padding-top: 20px;
        li {
            padding: 19px 0 0 50px;
            span {
                display: inline-block;
            }
            span:nth-child(even) {
                float: right;
                margin-right: 50px;
            }
            .isopen {
                font-family: PingFangSC-Regular;
                font-size: 15px;
                color: #FF511A;
                letter-spacing: 0;
            }
        }
    }
}
</style>



