<template>
    <div>
        <tip-page :tipTitle="title" :tipMsg="msg"></tip-page>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">完成</mt-button>
        </div>
    </div>
    
</template>

<script>


export default {
    data() {
        return {
            title: '开通成功',
            msg: '您已开通本账户在境外的pos消费功能'
        };
    },
    created(){
        var type = this.$route.query.type || '';
        if( type == 'close'){
            this.title = '关闭成功';
            this.msg = '您已关闭本账户在境外的pos消费功能';
        }
    },
    mounted(){
    },
    methods: {
        back (){
            this.$router.push('/');
        }
    }
};
</script>

<style lang="scss">
.field-action {
    margin-top: 66px;
}
</style>



