<template>
    <div class="device-revocation-row">
    <span style="display: block;padding: 10px 0 10px 10px;color: #329eef;">{{accountInfo.accountTypeName}}户需绑定账户：</span>
        <div class="device-revocation-detail">
            <mt-cell label="客户姓名：" :value="accountInfo.userName"></mt-cell>
            <mt-cell label="身份证号：" :value="accountInfo.userIdNo"></mt-cell>
            <mt-cell label="手机号：" :value="accountInfo.phone"></mt-cell>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">绑定账户信息：</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-cell class="mint-select" title="账户来源：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.ACCTSOURCE">
                    <option v-for="(item, index) in actionData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="账号：" placeholder="请输入账号" v-model="accountInfo.PAYEE_ACCOUNT"></mt-field>
            <mt-cell class="mint-select" title="账户类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.BINDACCTTYPE">
                    <option v-for="(item, index) in typeData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
            <mt-button @click="next()" size="large" type="primary">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/my/account-lift-level';

export default {
    data() {
        return {
            actionData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '1',
                    label: '本行'
                },
                {
                    value: '2',
                    label: '他行'
                }
            ],
            typeData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '2',
                    label: '借记卡'
                },
                {
                    value: '3',
                    label: '中国农业银行'
                }
            ],
            accountInfo: {}
        };
    },
    created() {
        this.accountInfo = Object.assign({}, this.$store.state.accountLiftLevel.search.accountInfo);
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.accountInfo);
            if (this.$validate.isEmpty(_data.ACCTSOURCE)) {
                this.doMsg('请选择账号来源');
                return false;
            }
            if (this.$validate.isEmpty(_data.PAYEE_ACCOUNT)) {
                this.doMsg('请输入账号');
                return false;
            }
            if (this.$validate.isEmpty(_data.OPERTYPE)) {
                this.doMsg('请选择账号类型');
                return false;
            }
            service.getAccountLiftLevelSuccess({
                'OPERTYPE': this.accountInfo.OPERTYPE,
                'PAYER_ACCOUNT': this.accountInfo.PAYER_ACCOUNT,
                'ACCTLEVEL': this.accountInfo.ACCTLEVEL,
                'PAYEE_ACCOUNT': this.accountInfo.PAYEE_ACCOUNT,
                'ACCTSOURCE': this.accountInfo.ACCTSOURCE,
                'BINDACCTTYPE': this.accountInfo.BINDACCTTYPE,
                'PAY_PWD': this.accountInfo.PAY_PWD
            }).then((res) => {
                this.$ls.setItem("accountAccountLiftLevelSuccess", res.data);
                this.$router.push('/account-lift-level/success');
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        doMsg(msg) {
            this.$toast(msg);
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



