<template>
    <div>
        <div class="cross-items" @click="toClick(item)" v-for="item in list">
            <span class="items-span-title">阜阳颍东农村商业银行</span>
            <span class="items-span-account">{{item.account}}</span>
            <span class="items-span-type" :class="{'items-span-type-close': item.isopen == 'false'}">工资卡</span>
            <div class="default" v-if="item.defaule=='true'">
                <div class="msg">主账户</div>
            </div>
        </div>
    </div>
    
</template>

<script>

import * as service from '@/services/my/cross-border-transaction';

export default {
    data() {
        return {
            list: []
        };
    },
    created(){
        service.getCrossBorderTransactionIndex().then((res) => {
            this.list = res.data.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        toClick (item ){
            this.$ls.setItem("crossBorderTransactionDetail", item);
            this.$router.push("pay");
        }
    }
};
</script>

<style lang="scss">
.cross-items {
    width: 690px;
    height: 238px;
    margin: 30px auto;
    position: relative;
    background-image: url('../../../../assets/img/bank/card/bank@2x.png');
    background-image: image-set(url('../../../../assets/img/bank/card/bank@3x.png') 3x);
    background-repeat: no-repeat;
    background-size: 100%;
    span {
        display: block;
        padding: 34px 0 0 86px;
    }
    .items-span-title {
        font-family: PingFangSC-Regular;
        line-height: 42px;
        padding-top: 28px;
        font-size: 15px;
        color: #333333;
        letter-spacing: 0;
    }
    .items-span-title:before {
        content: '';
        position: absolute;
        width: 36px;
        height: 36px;
        margin: 3px 0 0 -56px;
        background-image: url('../../../../assets/img/bank/card/logo@2x.png');
        background-image: image-set(url('../../../../assets/img/bank/card/logo@3x.png') 3x);
        background-repeat: no-repeat;
        background-size: 100%;
    }
    .items-span-account {
        font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
        line-height: 34px;
        font-size: 15px;
        color: #272727;
        letter-spacing: 2.14px;
    }
    .items-span-type {
        font-family: PingFangSC-Regular;
        line-height: 36px;
        opacity: 0.8;
        font-size: 13px;
        color: #333333;
        letter-spacing: 0;
    }
    .items-span-type:after {
        content: '';
        position: absolute;
        width: 126px;
        height: 42px;
        right: 30px;
        margin-top: -6px;
        background-image: url('../../../../assets/img/bank/card/open@2x.png');
        background-image: image-set(url('../../../../assets/img/bank/card/open@3x.png') 3x);
        background-repeat: no-repeat;
        background-size: 100%;
    }
    .items-span-type-close:after {
        background-image: url('../../../../assets/img/bank/card/close@2x.png');
        background-image: image-set(url('../../../../assets/img/bank/card/close@3x.png') 3x);
    }
}

.default {
    position: absolute;
    top: 0;
    right: 0;
    width: 84px;
    height: 84px;
    background-image: url('../../../../assets/img/bank/card/default@2x.png');
    background-image: image-set(url('../../../../assets/img/bank/card/default@3x.png') 3x);
    background-repeat: no-repeat;
    background-size: 100%;
    .msg {
        position: absolute;
        top: 24%;
        left: 12%;
        width: 100px;
        color: #fff;
        font-size: 10px;
        transform: rotate(45deg) scale(0.8);
    }
}
</style>



