import ajax from '@/services/ajax';
import api from '@/services/api';

export function getCrossBorderTransactionIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getCrossBorderTransactionIndex );
}

export function getCrossBorderTransactionPay(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getCrossBorderTransactionPay );
}

export function getCrossBorderTransactionSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getCrossBorderTransactionSuccess );
}




