<template>
	<div class="card-delete-success">
		<tip-page :tipTitle="tipTitle" :tipMsg="tipMsg" tipButton="完成" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipTitle: '',
			tipMsg: ''
		};
	},
	created() {
		if(this.$route.params.type === 'add'){
			this.tipTitle = '添加成功';
			this.tipMsg = '密码已校验，账户添加成功';
		} else{
			this.tipTitle = '删除成功';
			this.tipMsg = '您已从手机银行成功删除了该账号';
		}
	},
	methods: {
		back() {
			this.$router.go(-2);
		}
	}
};

</script>


