<template>
	<div class="my-bank-card-account">
		<div class="account-msg cell-part">
			<div class="name">{{account.ACCT_NAME}}</div>
			<div class="bank">
				<span class="name">{{account.FJGM}}</span>
				<span class="bank-type">{{account.ACCT_LEVEL}}</span>
			</div>
			<div class="number">{{account.CARD | formatAccount}}</div>
			<ul class="type">
				<li>人民币卡</li>
				<li>账户类型：{{account.ACCT_TYPE}}</li>
				<li>账户状态：{{account.ACCT_STATUS}}</li>
			</ul>
		</div>
		<div class="tip-hint">
			该账户状态异常，无法添加到手机银行，请到我行网点查询，或致电96669。
		</div>
		<div class="cell-part">
			<mt-field class="account-rename" label="设置别名" placeholder="（选填）请输入别名，例如：（理财卡）" v-model="rename" :attr="{maxlength: 10}"></mt-field>
		</div>
		<div class="cell-part">
			<cell-password :options="cellPassword" ></cell-password>
		</div>
		<div class="cell-warn">
			交易密码错误，请重新输入！
		</div>
		<div class="button-bottom">
			<mt-button @click="submit" size="large" type="primary">添加到手机银行</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			account: {},
			rename: '',
			password: '1111',
			cellPassword: {
                title: '交易密码',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			var _error = [];
			var self = this;
			if(this.rename && !this.$validate.isAlias(this.rename)){
				_error.push('别名输入格式不正确，请输入数字、字母或汉字');
			}
			if( this.$validate.isEmpty( this.password ) ) {
                _error.push( '交易密码不能为空。' );
            }
            if( _error.length > 0 ) {
            	this.$alert(_error[0], {
                    btn: { text: '确定' }
                });
                return;
            }
            service.addBankCardAccount({
            	ACCT_TYPE: '1',
            	PAYER_ACCOUNT: self.account.CARD,
            	ACCT_ALIAS: self.rename,
            	OPT_TYPE: '3',
            	PAY_PWD: 'sign:5A0E9068|rand:99D70B0F|epin:54D232B422B949D9|csn:411722197303195710|version:03|counter:6628F4F0'
            }).then(data => {
            	this.$router.push({name: 'MyBankCardSuccess', params: { type: 'add' }});
            });
		}
	},
	created(){
		this.account = this.$ls.getItem('bankCards');
		console.log(this.account);
	}
};

</script>

<style lang="scss">

.my-bank-card-account{
	.account-msg{
		position: relative;
		background-color: #fff;
		padding: 30px 50px 0;
		height: 284px;
		@include image-set('../../../../assets/img/bank/card/bg');
		.name{
			font-size: 15px;
			color: #666666;
			margin-bottom: 20px;
		}
		.bank{
			display: table;
			font-size: 15px;
			color: #666666;
			margin-bottom: 10px;
			.name{
				display: table-cell;
				padding-right: 20px;
				max-width: 560px;
				@include ellipsis();
			}
			.bank-type{
				position: relative;
				display: inline-block;
                width: 100px;
                height: 32px;
                font-size: 12px;
                color: #B5B6BE;
                text-align: center;
                line-height: 32px;
                @include image-set('../../../../assets/img/bank/card/label');
			}
		}
		.number{
			font-size: 15px;
			color: #272727;
			font-weight: normal;
            letter-spacing: 2.3px;
            font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
		}
		.type{
			position: absolute;
			display: table;
			width: 100%;
			left: 0;
			bottom: 20px;
			padding: 0 50px;
			font-size: 13px;
			color: #fff;
			li{
				display: table-cell;
			}
			li:nth-child(2){
				text-align: center;
			}
			li:nth-child(3){
				text-align: right;
			}
		}
	}
}

</style>


