<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="账号：" :value="accountInfo.PAYER_ACCOUNT"></mt-cell>
            <mt-cell label="账户类型：" :value="accountInfo.BINDACCTTYPE | accountTypeFilter"></mt-cell>
            <mt-cell label="绑定账户：" :value="accountInfo.PAYEE_ACCOUNT"></mt-cell>
            <mt-cell label="账户余额:" :value="accountInfo.AMOUNT"></mt-cell>
            <div class="detail-items">
                <cell-password :options=" cellPassword " ></cell-password>
            </div>
        </div>
        <div class="device-revocation-detail detail-items-li">
            <div class="detail-items">
                <span style="display:block"><b>温馨提示：</b></span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                1、若绑定账户为他行账户，请携本人身份证前往安徽农金网点办理销户。</span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2、若绑定账户为本行账户，Ⅱ、Ⅲ类账户销户后，账户余额及结息将自动转回绑定账户中。</span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                3、若绑定账户已销户，则无法操作此交易，请先进行绑定账户变更。</span>
            </div>
        </div>
        <div class="field-action inline">
            <mt-button @click="back()">返回</mt-button>
            <mt-button @click="next()">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/my/account-cancellation';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            accountInfo: {}
        };
    },
    created() {
        this.accountInfo = Object.assign(this.accountInfo, this.$store.state.accountCancellation.search.accountInfo);
        this.accountInfo.PAY_PWD = 'sign:214F9E71|rand:DB2D050F|epin:468DF7594A59C7A7|csn:340101199105050073|version:03|counter:24D2FAF0';
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            service.getAccountCancellationSuccess({
                'PAYER_ACCOUNT': this.accountInfo.PAYER_ACCOUNT,
                'AMOUNT': this.accountInfo.AMOUNT,
                'PAYEE_ACCOUNT': this.accountInfo.PAYEE_ACCOUNT,
                'ACCTSOURCE': this.accountInfo.ACCTSOURCE,
                'BINDACCTTYPE': this.accountInfo.BINDACCTTYPE,
                'PAY_PWD': this.accountInfo.PAY_PWD
            }).then((res) => {
                this.$ls.setItem("accountCancellationSuccess", res.data);
                this.$router.push('/account-cancellation/success');
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        doMsg(msg) {
            this.$toast(msg);
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



