<template>
	<div class="my-current">
		<div class="head">
			<div class="title">
				<span>活期总额（元）
					<a @click="showAmount" href="javascript:;" class="eye"></a>
				</span>
				
			</div>
			<div class="number">
				<span v-if="isShow">123456789</span>
				<span v-else>****</span>
			</div>

			<div class="mark">此活期总额为所有已添加的</div>
		</div>
		<div class="list">
			<a @click="detail" href="javascript:;" class="item">
				<div class="number">63044 **** 0989</div>
				<div class="money">
					活期 <span>66202.45</span>元
				</div>
				<div class="alias">
					<span class="line line-left"></span>
					<span class="msg">零用钱零用钱零用钱零用钱零用钱零用钱零用钱</span>
					<span class="line line-right"></span>
				</div>
			</a>
			<a @click="detail" href="javascript:;" class="item">
				<div class="number">63044 **** 0989</div>
				<div class="money">
					活期 <span>66202.45</span>元
				</div>
				<div class="alias">
					<span class="line line-left"></span>
					<span class="msg">零用钱零用钱零用钱零用钱零用钱零用钱零用钱</span>
					<span class="line line-right"></span>
				</div>
			</a>
		</div>
	</div>
</template>

<script>
	
export default{
	data() {
		return {
			isShow: true
		};
	},
	methods: {
		showAmount() {
			this.isShow = !this.isShow;
		},
		detail() {
			this.$router.push({name: 'TransactionDetail'});
		}
	}
};

</script>

<style lang="scss" scoped>

.my-current{
	.head{
		padding: 40px 0;
		height: 254px;
		text-align: center;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.03);
		@include image-set('../../assets/img/my/points-bg');
		.title{
			font-size: 13px;
			color: #B5B5B5;
			span{
				position: relative;
				.eye{
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					margin-left: 10px;
					width: 38px;
					height: 36px;
					&:after{
						content: '';
						position: absolute;
						top: 50%;
						left: 0;
						transform: translateY(-50%);
						width: 38px;
						height: 24px;
						@include image-set('../../assets/img/my/amount-show');
					}
				}
			}
		}
		.number{
			margin: 10px 0 20px;
			height: 72px;
			line-height: 72px;
			font-size: 30px;
			color: #272727;
		}
		.mark{
			font-size: 13px;
			color: #666666;
		}
	}
	.list{
		padding: 0 30px;
		.item{
			position: relative;
			display: block;
			margin-top: 30px;
			padding: 30px 36px 0;
			height: 216px;
			border-radius: 8px;
			@include image-set('../../assets/img/bank/card/bank');
			.number{
				position: relative;
				font-size: 15px;
				color: #272727;
				font-weight: normal;
                letter-spacing: 2.3px;
                font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
                &:after{
                	content: '';
                	position: absolute;
                	display: block;
                	top: 49%;
                	transform: translateY(-50%);
                	right: 0;
                	width: 32px;
                	height: 32px;
                	@include image-set('../../assets/img/my/to');
                }
			}
			.money{
				margin-top: 20px;
				height: 42px;
				line-height: 42px;
				font-size: 15px;
				color: #333333;
				span{
					color: #FF511A;
				}
			}
			.alias{
				position: absolute;
				padding: 0 80px;
				left: 50%;
				bottom: 30px;
				transform: translateX(-50%);
				width: 70%;
				height: 36px;
				text-align: center;
				overflow: hidden;
				.msg{
					display: inline-block;
					max-width: 300px;
					padding: 0 18px 0 26px;
					color: #989a9c;
					font-size: 12px;
					@include ellipsis();
				}
				.line{
					position: absolute;
					display: inline-block;
					top: 16px;
					width: 8px;
                    height: 8px;
                    background-color: rgba(166, 168, 176, 0.5);
                    border-radius: 50%;
                    &:after{
                    	content: '';
                        position: absolute;
                        display: block;
                        top: 4px;
                        width: 400px;
                        border-top: 1px solid rgba(166, 168, 176, 0.5);
                    }
				}
				.line-left:after{
                    right: 8px;
				}
				.line-right:after{
                    left: 8px;
				}
			}
		}
	}
}

</style>


