<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="客户姓名：" placeholder="请输入客户姓名" :value="accountInfo.USER_NAME"></mt-cell>
            <mt-cell label="身份证号码：" placeholder="请输入身份证号码" :value="accountInfo.IDCARD"></mt-cell>
            <mt-cell label="手机号：" placeholder="请输入手机号" :value="accountInfo.MOBILE"></mt-cell>
            <mt-cell class="mint-select" title="申请账户类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.ACCT_TYPE">
                    <option v-for="(item, index) in accountTypeData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">绑定账户信息:</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-cell class="mint-select" title="账户来源：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.ACCTSOURCE">
                    <option v-for="(item, index) in actionData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="账号：" placeholder="请输入申请账户" v-model="accountInfo.PAYER_ACCOUNT"></mt-field>
            <mt-cell class="mint-select" title="账户类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.BINDACCTTYPE">
                    <option v-for="(item, index) in typeData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <div style="height:50px;line-height:50px">
            <input type="checkbox" v-model="accountInfo.checkbox" style="width: 20px;height: 20px;margin-top: -2px;margin-bottom: 1px;vertical-align: middle;">
            <span style="color:red"><a href="#doc">我同意《个人开立电子结算账户申请协议书》</a></span>
        </div>
        <div class="device-revocation-detail">
            <div class="detail-items">
                <span style="display:block"><b>温馨提示：</b></span>
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Ⅱ类账户转账限额为日累计1万元，年累计20万元。Ⅲ类账户转账限额为日累计5000元，年累计10万元，账户余额不可大于1000元。绑定账户需为本人本行或他行的Ⅰ类账户或信用卡账户。</span>
            </div>
        </div>
        <div class="field-action inline">
            <mt-button @click="back()">返回</mt-button>
            <mt-button @click="next()" :disabled="!submitStatus" size="large">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/my/account-opening';
// import { mapGetters, mapActions, mapState } from 'vuex';

export default {
    data() {
        return {
            accountTypeData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: 'DEPS1002',
                    label: 'Ⅱ类账户'
                },
                {
                    value: 'DEPS1003',
                    label: 'Ⅲ类账户'
                }
            ],
            actionData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '0',
                    label: '本行'
                },
                {
                    value: '1',
                    label: '他行'
                }
            ],
            typeData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '2',
                    label: '借记卡'
                },
                {
                    value: '3',
                    label: '中国农业银行'
                }
            ],
            submitStatus: false,
            accountInfo: {
                USER_NAME: '', // 姓名
                IDCARD: '', // 身份证
                MOBILE: '', // 手机号
                ACCT_TYPE: '', // 账户类型
                PAYER_ACCOUNT: '', // 账号
                ACCTSOURCE: '', // 账户来源
                BINDACCTTYPE: '', // 绑定账户类型
                PAY_PWD: '', // 支付密码
                checkbox: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign({}, this.$store.state.accountOpening.search);
        var id = this.$route.query.id || '';
        service.getAccountOpeningIndex().then((res) => {
            this.accountInfo.USER_NAME = res.USER_NAME;
            this.accountInfo.IDCARD = res.IDCARD;
            this.accountInfo.MOBILE = res.MOBILE;
        }, (err) => {
            this.$alert(err.MESSAGE, {
                btn: { text: '完成' }
            });
        });
    },
    computed: {
    },
    components: {
    },
    watch: {
        accountInfo: {
            handler(item){
                if(item.PAYER_ACCOUNT && item.ACCT_TYPE && item.ACCTSOURCE && item.BINDACCTTYPE){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.accountInfo);
            if (this.$validate.isEmpty(_data.ACCT_TYPE)) {
                this.doMsg('请选择申请账户类型');
                return false;
            }
            if (this.$validate.isEmpty(_data.ACCTSOURCE)) {
                this.doMsg('请选择账户来源');
                return false;
            }
            if (this.$validate.isEmpty(_data.PAYER_ACCOUNT)) {
                this.doMsg('请输入申请账户');
                return false;
            }
            if (this.$validate.isEmpty(_data.BINDACCTTYPE)) {
                this.doMsg('请选择账户类型');
                return false;
            }
            if (!_data.checkbox) {
                this.doMsg('请先同意协议书');
                return false;
            }
            this.$router.push('/account-opening/pay');
        },
        doMsg(msg) {
            this.$toast(msg);
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateAccountOpeningSearch', this.accountInfo );
        }else{
            this.$store.dispatch('clearAccountOpeningSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.mint-cell.mint-select .mint-cell-title {
    width: 250px;
}
.device-revocation-row {
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



