<template>
	<div class="my-bank-card-add">
		<div class="cell-part">
			<div class="head-hint hint-reset">
				请输入您需要添加的银行卡号
			</div>
			<mt-field label="卡号" placeholder="请输入银行卡号" v-model="account" :attr="{maxlength:19}"></mt-field>
		</div>
		<div class="cell-part">
			<div class="head-hint">
				请输入交易密码
			</div>
			<mt-field label="交易密码" placeholder="" v-model="password"></mt-field>
		</div>
		<div class="tip-hint">
			交易密码错误，请重新输入！
		</div>
		<div class="button-bottom">
			<mt-button @click="submit" type="primary" size="large" :disabled="submitStatus">确认添加</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default{
	data() {
		return {
			account: '',
			password: '',
			submitStatus: false
		};
	},
	methods: {
		submit() {
			service.addBankCardAccount({
				ACCT_TYPE: '1',
            	PAYER_ACCOUNT: this.account,
            	OPT_TYPE: '3',
            	PAY_PWD: 'sign:5A0E9068|rand:99D70B0F|epin:54D232B422B949D9|csn:411722197303195710|version:03|counter:6628F4F0'
			}).then(data => {
				this.$router.go(-1);
			}, err => {
				this.$alert('此账号状态异常，请到我行网点咨询，或致电96669获取帮助', {
	                btn: { text: '确定' }
	            });
			});
		}
	}
};

</script>

<style lang="scss">

.my-bank-card-add{
	.hint-reset{
		margin-top: 0px;
		padding-top: 20px;
	}
}

</style>


