<template>
    <div>
        <div class="field-part">
            <cell class="item" title="密码管理" to="" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/my/set-up/setup-pwd@2x.png" >
            </cell>
            <cell class="item" title="限额管理" to="limit-manage/index" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/my/set-up/setup-xe@2x.png" >
            </cell>
            <cell class="item" title="跨境交易管理" to="cross-border-transaction/index" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/my/set-up/setup-kj@2x.png" >
            </cell>
        </div>
        <div class="field-part">
            <cell class="item" title="设备解绑" @click.native="toClick('device')" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/my/set-up/setup-sb@2x.png" >
            </cell>
            <cell class="item" title="清除缓存" to="" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/my/set-up/setup-hc@2x.png" >
            </cell>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>

// import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定解绑',
                btnGetText: '获取验证码'
            }
        };
    },
    created(){
    },
    mounted(){
    },
    methods: {
        toClick ( type ) {
            if( type == 'device'){
                this.$confirm('您正在解绑当前设备，解绑后将退出登录，再次登录需要重新绑定设备', {
                    yes: { text: '解绑' },
                    no: { text: '取消' }
                }).then((result)=>{
                    this.showDialogSmsVerify();
                }, (result)=>{
                    console.log(result);
                });
            }
        },
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
            }, (data) => {
            });
        }
    }
};
</script>

<style lang="scss">

</style>



